import { Component, OnInit } from '@angular/core';
import { environment } from '../../../projects/connect/src/environments/environment';

import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.css'],
})
export class BannerComponent implements OnInit {
  bannerImage = environment.bannerImage;

  private _jsonURL = 'http://localhost:3000/config.json';
  private _jsonURLUpload = 'http://localhost:3000/upload';

  objFile: any;
  uploadDone: any;

  bannerURL = '';

  constructor(private http: HttpClient) {


  }

  getObject(){
    console.log('dataaaaa11111');
    fetch(this._jsonURL).then(res => res.json())
    .then(json => {
      this.objFile = json;
      console.log('dataaaaa',json);
    });

  }
  upload(){
    this.uploadDone='';
    fetch(this._jsonURLUpload).then(res => res.text())
    .then(json => {
      this.uploadDone = 'done';
    });

  }

  ngOnInit(): void {
    this.http
      .get(environment.bannerNewUrl, { responseType: 'text' })
      .subscribe((data) => {
        this.bannerURL = data.split('=')[1];
      });
  }
}
