
 import {Injectable} from '@angular/core';
 import {DataService} from '../../app/Services/DataService/data.service';
 import {IApiData} from '../../app/Models/Interfaces/APIs/apiData.model';
 import {IAddDoctor} from '../../app/Models/Interfaces/AddDoctor/Adddoctor';
 import {IMainResponse} from '../../app/Models/Interfaces/Main/MainResponse';
 import {Observable} from 'rxjs';
 import {APIName} from '../../../../../shared/API/APIName';
 
 @Injectable({
   providedIn: 'root'
 })
 export class addDoctorService {
   /**
    * The Requirements For The Construction Of The service
    * @param dataService for managing Http methods
    */
   constructor(private dataService: DataService) {
   }
   /**
    * send addDoctor
    * @param adddoctorOBJ addDoctor object
    * @return Observable<IMainResponse>
    */
   sendaddDoctor(adddoctorOBJ: IApiData<IAddDoctor>): Observable<IMainResponse> {
     return this.dataService.post(APIName.adddoctor, adddoctorOBJ);
   }
 }
 