import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class DownloadService {
  fileName!: string;

  constructor() {
  }

  downLoadFile(data: any): any {
    const blob = new Blob([data.body]);
    const url = window.URL.createObjectURL(blob);
    this.fileName = data.headers?.get('Content-Disposition')?.split(';')[1].split('filename')[1].split('=')[1].trim();
    const link = document.createElement('a');
    link.href = url;
    if (!this.fileName) {
      return false;
    }
    link.download = JSON.parse(this.fileName);
    link.click();
    link.remove();
  }
}
